@import 'partials/variables';
@import 'partials/fonts';
@import 'partials/corner-border';
@import 'partials/buttons';
@import 'partials/panels';
@import 'partials/slider';
@import 'partials/loader';

body {
	font-family: 'Raleway', sans-serif;
	background-color: #fff;
	color: #121212;
	position: relative;
	margin: 0;
	min-height: 100%;
}

.navbar {
	background-color: transparent;
	border: 0;
}
.navbar-default .navbar-collapse{
	border: 0;
}
.navbar-default .navbar-nav>li>a {
	color: #fff;
	font-size: 16px;
}
.nav>li {
    color: #fff;
}
.nav>li>img{
	padding-left: 15px;
}
.navbar-default .navbar-toggle {
    border: 0;
}
.navbar-default .navbar-toggle .icon-bar {
    background-color: #fff;
}

.navbar-fixed-top.scrolled {
	background-color: #333 !important;
	transition: background-color 200ms linear;
  }

.navbar-default .navbar-toggle:focus, .navbar-default .navbar-toggle:hover {
    background-color: transparent;
}
.navbar-brand > img {
	height: 55px;
}

.navbar-default .navbar-nav>li>a:hover {
    color: #fff;
    text-decoration: underline;
    background-color: transparent;
}

.container-split {
	width: 100vw;
	height: 100vh;
	display: flex;
}

.container-split .split-left-content {
	width: 35%;
	min-height: 100vh;
	display: flex;
	align-items: center;
	justify-content: flex-end;
	overflow: auto;
	background: #333;
}

.container-split .split-image {
	width: 65%;
	background-image: url('../img/PEMEX.jpg');
	background-size: 127%;
}

.container-split .header-section {
	position: absolute;
	text-align: center;
	width: 100%;
	top: 4%;
}

#testimonios{
	min-height: 510px;
}

.left-header-panel {
	background-color: #DADADA;
	margin: 0;
	border: 0;
	padding: 45px;
	text-align: center;
	p {
		font-size: 20px;
		color: $orange;
	}
	span {
		font-size: 16px;
	}
}

.panel-cotizador {
	background-color: rgba(32, 107, 157,0.7);
	margin: 27% 70px 0 25px;
	height: auto;
	color: #fff;
}

.title-cotizador {
	text-align: center;
    padding: 26px;
    font-size: 16px;
}

.barra-cotizador {
	margin: 20px 0 67px 0;
}

#cotizador {
	margin-top: -30px;
}

.steps-info img {
	margin: 0 auto;
	height: 75px; }

.steps-title {
	font-size: 18px;
	padding: 15px 0;
}

.steps-title:before, .steps-title:after {
	content: '';
	position: absolute;
	top: 129px;
	left: 60%;
	margin-left: -23%;
	width: 24%;
	height: 2px;
	background: $orange;
}

.steps-info p {
    font-size: 13px;
    padding: 10px 0;
}

.icon-item img {
	height: 35px;
	}

.testimonials {
	font-family: 'Raleway', sans-serif;
	text-align: center;

	.section-header {
		width: 100%;
		background: $red-1;

		h1 {
			color: #ffffff;
			font-size: 36px;
			padding: 15px 0;
		}
	}
}

.split-image-testimony {
	background-image: url('../img/dinero.jpg');
	background-size: cover;
	height: 75%;
}

.split-image-testimony h2 {
	color: $orange;
	font-size: 38px;
}

.split-image-testimony h2:before, .split-image-testimony h2:after {
	content: '';
	position: absolute;
	top: 71px;
    left: 28%;
	margin-left: -23%;
	width: 24%;
	height: 1px;
	background:#00A2AE;
}

.split-right-content {
	background-color: #333;
	height: 75%;
}

.carousel {
	position: absolute;
    width: 100%;
}

.testimonio-pic {
	border-radius: 50%;
	height: 100px;
}

.person img {
	height: 48px;
    position: absolute;
    left: 26%;
}

.person p {
	line-height: 40px;
}

.item .panel {
	margin-top: 85px;
    background: rgba(255,255,255,0.8);
}

.faq h1 {
	text-transform: uppercase;
	color: $orange;
}

.faq h1 span {
	display: block;
	color: #333;
}

.form-group input, select.form-control {
    padding: 10px 30px;
    background: transparent;
    border-radius: 12px;
    outline: none;
    -webkit-appearance: none;
    font-size: 16px;
    font-weight: bold;
    color: #fff;
    letter-spacing: 0;
    line-height: 16px;
    box-shadow: none;
    border: 1px solid #333;
}

.form-control::-webkit-input-placeholder {
	color: #fff;
	font-size: 16px;
}

.form-control:focus {
	border-color: #333;
    outline: 0;
    -webkit-box-shadow: none;
    box-shadow: none;
}

.wrapper {
	min-height: 150vh;
}

.pago-por-mes {
	border: 1px solid #333;
    border-radius: 12px;
    text-align: center;
    padding: 4px 0;
}

.pago-por-mes p {
	color: #bfbfbf;
	font-size: 14px;
	margin: 0;
}

.pago-por-mes span {
	font-size: 11px;
}

#cel-credit {
	background-color: #C0BFBB;
	text-align: center;
	padding: 50px 0;
}

.cel-legend {
	font-size: 2em;
	color: #979797;
	text-align: center;
    padding: 15px 20px;
}

.check-status {
	text-align: center;
    border: 0;
    border-bottom: 1px solid $orange;
    background: transparent;
    color: #e3e3e3;
    font-size: 36px;
}

.check-status:focus {
    outline: 0;
    -webkit-box-shadow: none;
    box-shadow: none;
}

.check-status::-webkit-input-placeholder {
	color: #e3e3e3;
}

.btn-form {
	color: #fff;
    border-radius: 12px;
    background: $orange;
    font-size: 18px;
    margin: 20px 0;
}

.margin-7 {
	margin: 7% 0;
}

.no-margin {
	margin: 0;
}

.steps-header h2 {
	font-size: 42px;
	color: $orange;
	font-weight: 800;
	margin-top: 30px;
}

.steps-header span {
	font-size: 18px;
	color: rgb(116, 112, 112);
}

.steps-info {
	padding: 30px 0;
}

#landing-section {
	text-align: center;
	height: 100vh;
}

#footer {
	width: 100%;
	background-color: #333;
}

.footer-top {
	padding: 20px;
	text-align: left;
}
.footer-bottom {
	height: 118px;
	padding: 10px 0;
}

.footer-bottom p {
	font-size: 14px;
	color: white;
}

.footer-top .question-contact p {
	font-size: 24px;
	color: #FFFFFF;
	letter-spacing: 0;
}

.footer-bottom a {
	color: white;
	height: 20px;
	margin: 10px 8px;
}

.footer-bottom .text-footer-bottom {
	font-family: 'Raleway', sans-serif;
	font-size: 100%;
	color: #FFFFFF;
	letter-spacing: 0;
	line-height: 28px;
}



.footer-bottom .text-footer-bottom a{
	color: #FFFFFF;
	text-decoration: none;
}

.scrollmenu {
	text-transform: none;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	text-align: center;
	box-shadow: 0 2px 2px 0 rgba(0,0,0,0.20);
}

.scrollmenu a {
	-webkit-box-flex: 1;
	-ms-flex: 1;
	flex: 1;
	padding: 20px;
	text-decoration: none;
	color: #4E6E82;
	font-size: 14px;
}

.scrollmenu .fa {
	color: $red-1;
	opacity: 0;
	transform: rotate(90deg);
}

.scrollmenu a.active {
	color: #121212;
}

.scrollmenu a.active .fa {
	opacity: 1;
}

.solicitud-container {
	background: #ffffff;
	// width: 100%;
	// height: auto;
	// position: absolute;
	// top: 81px;
	// display: block;
}

.solicitud-container .form-control {
	width: 100%;
	margin-bottom: 35px;
}

.historial-crediticio img {
	padding-bottom: 15px;
}

.historial-crediticio p {
	font-family: 'Raleway', sans-serif;
	font-size: 14px;
	color: #121212;
	letter-spacing: 0;
	line-height: 16px;
}

.radio-input-solicitud {
	width: 50px;
	position: relative;
	display: inline-block;
	text-align: center;
}

.radio-input-solicitud label {
	border: 1px solid #979797;
	border-radius: 50%;
	padding: 10px;
	width: 40px;
	font-family: 'Raleway', sans-serif;
	font-size: 11px;
	line-height: 16px;
	color: #4E4044;
}

.radio-input-solicitud input:checked + label {
	color: #FFA400;
	border: 1px solid #FFA400;
}

.red-link a{
    color: #EB0029 !important;
    text-decoration: none;
    margin: 10px;
    display: block;
}

.busca-sucursal {
	color: #EB0029 !important;
	text-decoration: none;
	margin: 10px;
	display: block;
	font-family: 'Raleway', sans-serif;
	font-size: 20px;
}

.busca-sucursal:hover {
	text-decoration: none;
}

.busca-sucursal .fa {
	font-size: 32px;
}

.margin-40 {
 		margin: 40px;
}

.icons-row p.question-title {
	font-family: 'Gotham';
	font-size: 20px;
	color: #4E4044;
	text-align: left;
	padding-bottom: 15px;
}

.icons-row img {
	margin: 0 auto;
	display: block;
}

.icons-row p {
	padding: 15px 0;
	text-align: center;
	font-family: 'Gotham';
	font-size: 14px;
	color: #4E4044;
	letter-spacing: 0;
	line-height: 16px;
}

.proceso {
	margin-top: 60px;
	background-image: url("../img/steps-bg.png");
	background-repeat: no-repeat;
	background-size: cover;
}

.testimonials {
	text-align: center;
	min-height: 449px;
	background-image: url("../img/testimoni-bg.png");
	background-repeat: no-repeat;
	background-size: cover;
}

#cotizador svg {
	max-height: 300px;
	margin: 0 auto;
	/* poor attempt at centering */
	height: auto !important;
	/* overrides inline */
	width: auto !important;
	/* overrides inline */
	max-width: 300px;
	display: block;
}

.active svg path {
	fill: #FFA400 !important;
}

#icono-elimina-ambos {
	color: $red-1;
	padding-top: 60px;
}

#icono-elimina-ambos img {
	display: inline-block;
}

.solicitud {
	margin-top: -42px;
	position: relative;
}

.solicitud .nav-tabs {
	display: flex;
	text-align: center;
}

.solicitud .nav-tabs > li > a {
	background: #DADADA;
	font-size: 16px;
	color: #9B9B9B;
	margin-right: 2px;
	line-height: 1.42857143;
	border: 0;
	border-radius: 0;
}

.solicitud .nav-tabs > li {
	flex: 1;
}

.solicitud .nav-tabs > li.active > a,
.solicitud .nav-tabs > li.active > a:focus,
.solicitud .nav-tabs > li.active > a:hover {
	background: #FFFFFF;
	box-shadow: 0 2px 4px 0 rgba(0,0,0,0.20);
	font-size: 16px;
	color: #000000;
	cursor: default;
	background-color: #fff;
	border: 0;
	border-bottom-color: transparent;
}

.solicitud .tab-content {
	background: #FFFFFF;
	box-shadow: 2px 2px 4px 0 rgba(0,0,0,0.20);
	border-radius: 8px;
	padding-bottom: 50px;
	padding-top: 20px;
	margin-bottom: 50px;
	display: inline-block;
}

.nav-solicitud {
	background-color: #F4F4F4 !important;
	height: 200px !important;
	background-image: url("../img/couple-bg.png");
	background-repeat: no-repeat;
	background-size: cover;
	background-position-y: 35%;
}

.navbar.nav-solicitud {
	position: relative !important;
}

.nav-folio {
	padding-right: 300px;
	line-height: 60px;
	font-size: 16px;
	color: $red-1;
	font-family: 'Gotham medium';
}

.margin-top-24 {
	margin-top: 24px;
}

.padding-bottom-20 {
	padding-bottom: 20px;
}

.tab-content>.active {
    padding-bottom: 20px;
}

.opciones-crediticias p {
	font-size: 11px;
	font-family: 'Gotham medium';
}

.red {
	color: $red-1;
}

#copy-clipboard {
	cursor: pointer;
}

.tooltip-folio .tooltiptext {
	visibility: hidden;
	width: 140px;
	background-color: #555;
	color: #fff;
	text-align: center;
	border-radius: 6px;
	padding: 5px;
	position: relative;
	z-index: 1;
	bottom: 0;
	left: 10%;
	margin-left: -75px;
	opacity: 0;
	transition: opacity 0.3s;
}

.tooltip-folio:hover .tooltiptext {
	visibility: visible;
	opacity: 1;
}

.opciones-crediticias {
	margin: 15px 0;
}

.faq-answer {
	border-top: 1px solid #e3e3e3;
	border-bottom: 1px solid #e3e3e3;
	padding: 20px;
	font-family: 'Raleway', sans-serif;
	color: #4E4044;
}


.icon-list p.list-requisitos, .line-after ~ p {
	font-family: 'Raleway', sans-serif;
	padding: 6px 0;
}

.icon-list p.list-requisitos span{
	padding-right: 10px;
	width: 36px;
	display: inline-block;
}

.input-select-cita {
	padding: 10px 0;
	display: inline-flex;
}

.input-select-cita .form-control {
	text-align: right;
}

.input-select-cita img {
	padding-left: 3px;
}

.input-select-cita .form-control span {
	float: left;
}
.width-200 {
	width: 200;
	    margin: 10px auto;
}
.margin-bottom-30 {
	margin-bottom: 30px;
}
.form-group {
    margin-bottom: 25px;
}

.form-group p{
	padding-top: 10px;
}

.select-group label {
	font-size: .9em;
  float: left;
}
/* importamos responsive al final */
@import 'partials/responsive';
