.btn-red {
  background: $red-1;
  border-radius: 0;
  margin: 5px 0;
  padding: 10px 9px;
  font-family: 'Gotham medium';
  font-size: 13px;
  color: #FFFFFF;
  letter-spacing: 0;
  line-height: 16px;
  text-align: center;
  width: 205px;
}

.btn-small {
  margin: 0;
  padding: 7px 10px;
  text-align: center;
}

.btn-block {
  width: 100%;
}

.btn-flat {
  border-radius: 0;
}

.btn-red:hover {
  color: #ffffff;
}

.btn-red .fa{
  float: right;
}

.consulta-folio-action {
  position: absolute;
right: 85px;
top: 55px;
color: $red-1;
cursor: pointer;
}
