.slider-navigation .prev.slick-disabled:before {
  content: url('../img/arrow_left_disabled.png');
}

.slider-navigation .next.slick-disabled:before {
  content: url('../img/arrow_right_disabled.png');
}

.slider-navigation .prev:before {
  content: url('../img/arrow_left_active.png');
}

.slider-navigation .next:before {
  content: url('../img/arrow_right_active.png');
}

.carousel-indicators {
    bottom: -50px;
}

.carousel-control.left, .carousel-control.rig {
    background: transparent;
}


.carousel-control {
  background: transparent;
  position: relative;
  display: inline-block;
  color: #fff;
  text-align: center;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.6);
  background-color: transparent;
  opacity: 1;
  padding-top: 20px;
  width: 45px;
}

.carousel-control.left:before {
  content: url('../img/arrow_left_white.png');
}

.carousel-control.right:before {
  content: url('../img/arrow_right_white.png');
}

.slider-container {
  width: 100%;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
  margin-top: 33px;
}
.slider-container .back-bar {
  height: 10px;
  position: relative;
}
.slider-container .back-bar .selected-bar {
  position: absolute;
  height: 100%;
}
.slider-container .back-bar .pointer {
  position: absolute;
  width: 10px;
  height: 10px;
  background-color: red;
  cursor: col-resize;
  opacity: 1;
  z-index: 2;
}
.slider-container .back-bar .pointer.last-active {
  z-index: 3;
}
.slider-container .back-bar .pointer-label {
  position: absolute;
  top: -17px;
  font-size: 8px;
  background: white;
  white-space: nowrap;
  line-height: 1;
}
.slider-container .back-bar .focused {
  z-index: 10;
}
.slider-container .clickable-dummy {
  cursor: pointer;
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 1;
}
.slider-container .scale {
  top: -18px;
  position: relative;
}
.slider-container .scale span {
  position: absolute;
  height: 8px;
  border-left: 1px solid #fff;
  font-size: 0;
}
.slider-container .scale ins {
  font-size: 12px;
  text-decoration: none;
  position: absolute;
  left: -7;
  top: -14px;
  color: #fff;
  line-height: 1;
}
.slider-container.slider-readonly .clickable-dummy,
.slider-container.slider-readonly .pointer {
  cursor: auto;
}
