.panel-tarjeta-title {
  padding: 20px 0 10px 0;
    font-family: 'Gotham medium';
    color: $red-1;
}

.remove-symbol {
  position: absolute;
top: -12px;
right: 53px;
}

.tab-content .panel {
  border: 0;
  box-shadow: none
}

.panel-default.active>.panel-heading {
    display: none;
}

.panel-default>.panel-heading {
    color: #FFFFFF;
    background-color: #D4451D;

}

.orange-1 {
  background: #FF671B;
  box-shadow: 0 2px 4px 0 rgba(212,69,29,0.50);
}

.orange-2 {
  background: #D4451D;
box-shadow: 0 2px 4px 0 rgba(212,69,29,0.50);
}

.yellow-1 {
  background: #FFA400;
box-shadow: 0 2px 4px 0 rgba(255,164,0,0.50);
}

.morada {
  background: #B01657;
box-shadow: 0 2px 4px 0 rgba(235,0,41,0.50);
}

.panel-tarjeta-text p {
  padding-top: 5px;
  text-align: left;
  font-size: 14px;
  color: #4E4044;
  letter-spacing: 0;
  line-height: 16px;
}

.panel-tarjeta-text p.line-after {
  padding: 15px 0;
}

.panel-tarjeta-text .line-after:before {
  display: block;
  content: "";
  width: 40px;
  position: relative;
  top: 25px;
  left: 0;
  border-top: 1px solid $red-1;
  border-left: 1px solid $red-1;
}

.panel-tarjeta-text a {
  color: $red-1;
  margin: 10px 0;
  text-decoration: none;
}

.header-calculate-options {
  background: #FFFFFF;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.20);
  height: 90px;
  text-align: center;
  padding: 35px;
}

.header-calculate-options:hover:before {
  display: block;
  content: "";
  width: 20px;
  height: 20px;
  position: absolute;
  top: 0;
  left: 0;
  border-top: 1px solid $red-1;
  border-left: 1px solid $red-1;
}

.header-calculate-options:hover:after {
  content: "";
  width: 20px;
  height: 20px;
  position: absolute;
  top: 0;
  right: 0;
  border-top: 1px solid $red-1;
  border-right: 1px solid $red-1;
}

.header-calculate-options a {
  font-family: 'Ubuntu', sans-serif;
  font-size: 14px;
  color: #4E6E82;
  letter-spacing: 0;
  line-height: 20px;
}

.header-calculate-options:hover a {
  text-decoration: none;
  color: #121212;
}

.header-calculate-options:hover a:before {
  display: block;
  content: "";
  width: 20px;
  height: 20px;
  position: absolute;
  bottom: 0;
  left: 0;
  border-bottom: 1px solid $red-1;
  border-left: 1px solid $red-1;
}

.header-calculate-options:hover a:after {
  display: block;
  content: "";
  width: 20px;
  height: 20px;
  position: absolute;
  bottom: 0;
  right: 0;
  border-bottom: 1px solid $red-1;
  border-right: 1px solid $red-1;
}

.cotizador-input {
  font-family: 'Ubuntu', sans-serif;
  background: #FFFFFF;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,0.20);
  min-height: 360px;
  text-align: center;
  padding: 0 25px;
}

.cotizador-input img {
  padding: 20px 0;
}

.cotizador-input span {
  cursor: pointer;
}

.cotizador-input h2 {
  font-family: 'Gotham';
  font-size: 24px;
  color: #4E6E82;
  letter-spacing: 0;
  line-height: 28px;
}

.cotizador-input .form-input {
  width: 85%;
  margin: 0 auto;
}

