@font-face {
  font-family: 'Gotham';
  src: url('../fonts/Gotham-Light.eot');
  src: local('☺'), url('../fonts/Gotham-Light.woff') format('woff'), url('../fonts/Gotham-Light.ttf') format('truetype'), url('../fonts/Gotham-Light.svg#webfont') format('svg');
}

@font-face {
  font-family: 'Gotham medium';
  src: url('../fonts/gotham-medium.eot');
  src: local('☺'), url('../fonts/gotham-medium.woff') format('woff'), url('../fonts/gotham-medium.ttf') format('truetype'), url('../fonts/gotham-medium.svg#webfont') format('svg');
}

.navbar-brand {
  color: #121212 !important;
}

.navbar-brand span {
  color: $red-1;
}

.header-section h4, .precalificador-section h4 {
  font-family: 'Gotham';
  font-size: 43px;
  color: #ffffff;
  text-shadow: 1px 2px 0 #333;
  text-transform: uppercase;
}

.header-section h4:before {
  display: block;
  content: "";
  width: 132px;
  position: relative;
  top: 54px;
  left: 21%;
  border-top: 1px solid $orange;
  border-left: 1px solid $orange;
}

span.action {
  font-family: 'Ubuntu', sans-serif;
  font-size: 24px;
  color: #4E6E82;
  letter-spacing: 0;
  line-height: 30px;
  display: block;
}

#cotizador h1, .faq h1 {
  font-family: 'Gotham', sans-serif;
  font-weight: 100;
  font-size: 30px;
color: #4E4044;
padding-top: 15px;
}

.testimonials h1 {
  font-size: 36px;
color: #FFFFFF;
padding: 20px 0;
text-align: center;
}

.quote {
  font-family: 'Gotham medium';
  font-size: 36px;
color: #FFFFFF;
letter-spacing: 0;
line-height: 40px;
padding: 60px 0 35px 0;
}

.quote-person {
  font-family: 'Gotham', sans-serif;
  font-size: 24px;
color: #FFFFFF;
letter-spacing: 0;
}

.faq h1:before {
  display: block;
  content: "";
  width: 133px;
  height: 100px;
  position: absolute;
  top: 19px;
  right: 150px;
  border-top: 1px solid $orange;
  border-right: 1px solid $orange;
  border-bottom: 1px solid $orange;
}


.cotizador-big-title {
  font-size: 14px;
  color: #121212;
  letter-spacing: 0;
  line-height: 20px;
}
.cotizador-valor-md {
  font-family: 'Gotham';
  font-size: 18px;
  color: #EB0029;
}

.cotizador-results p {
  color: $red-1;
  padding: 10px 0;
}

.cotizador-results .radio-year-label {
  color: #121212 !important;
}

.faq {
  padding-bottom: 40px;
}
.faq a {
  color:  #333;
  text-decoration: none;
  font-family: 'Gotham';
  font-weight: 100;
  display: block;
  padding: 10px 0;
  font-size: 20px;
}

.title-section {
  font-family: 'Gotham medium';
  font-size: 36px;
color: #4E4044;
letter-spacing: 0;
display: block;
}

.solicitud-container h2 {
font-family: 'Ubuntu', sans-serif;
  font-size: 36px;
color: #4E6E82;
letter-spacing: 0;
}

.solicitud-container h2 span {
  color: $red-1;
}

.panel-fecha-cita p{
  text-align: center;
  font-family: 'Gotham';
font-size: 14px;
color: #4E4044;
letter-spacing: 0;
line-height: 16px;
}

span.dia-calendario {
  background: #FFA400;
    box-shadow: 0 3px 9px 0 rgba(237,84,84,0.40);
    padding: 4px;
    border-radius: 50%;
    width: 25px;
    height: 25px;
    display: inline-block;
    text-align: center;
    color: #ffffff;
    font-size: 12px;
    float: left;
}

.solicitud-container h4 {
  font-family: 'Ubuntu', sans-serif;
  font-size: 24px;
color: #4E6E82;
letter-spacing: 0;
padding: 25px 0;
}

.solicitud-container hr {
  box-shadow: 0 1px 2px 0 rgba(0,0,0,0.20);
}
