@media (min-width:1115px) {
	.navbar-nav > li {
		padding: 20px 10px 15px 18px;
	}
}

@media (max-width: 435px) {
	.navbar-brand > img {
		height: 30px;
	}
	.container-split {
		height: auto;
		flex-direction: column;
	}
	.container-split .split-left-content {
		width: 100%;
		min-height: 55vh;
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-align: center;
		-ms-flex-align: center;
		align-items: flex-end;
		-ms-flex-pack: end;
		justify-content: center;
	}
	.container-split .split-image {
		width: 100%;
		background-size: 145%;
		background-repeat: no-repeat;
	}
	.container-split .header-section {
		top: 2%;
	}
	.header-section h4 {
		font-size: 27px;
	}
	.panel-cotizador {
		margin: 5% 8px 0 8px;
		padding: 0 8px;
	}
	.split-right-content {
		display: none;
	}
	.faq h1:before {
		top: -1px;
		right: 125px;
	}
	.header-section h4:before {
		width: 97px;
		top: 34px;
		left: 5%;
	}

}

@media (min-width:801px) {
		.navbar-nav {
			float: right;
		}
		

	
	
	
}
